.contact_container{
    grid-template-columns: repeat(2,max-content);
    justify-content: center;
    column-gap: 6rem;
    padding-bottom: 3rem;
}
.contact_info{
    display: grid;
    row-gap: 1rem;
    grid-template-columns: 300px;
}
.contact_form{
    width: 360px;
}
.contact_title{
    text-align: center;
    font-size: var(--h3-font-size);
    font-weight: var(--font-medium);
    margin-bottom: var(--mb-1-5);
}
.contact_card-icon{
    font-size: 2rem;
    color: var(--title-color);
    margin-bottom: var(--mb-0-25);
}
.contact_form{
    width: 360px;
}
.contact_card-title,
.contact_card-data{
    font-size: var(--font-medium);
}
.contact_card-data{
    display: block;
    margin-bottom: 10px;
}
.contact_button{
    color: var(--text-color);
    font-size: var(--small-font-size);
    display: inline-flex;
    align-items: center;
    justify-content: center;
    column-gap: 0.25rem;
}
.contact_card{
    background-color: var(--container-color);
    border: 1px solid rgba(0, 0, 0, 0.1);
    padding: 1rem;
    border-radius: 0.75rem;
    text-align: center;
}
.contact_form-div{
    position: relative;
    margin-bottom: var(--mb-2);
    height: 4rem;
}
.contact_form-input{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 2px solid rgba(0, 0, 0, 0.3);
    background: none;
    outline: none;
    border-radius: 0.75rem;
    padding: 1.5rem;
    z-index: 1;
    color: var(--text-color);
}
.contact_form-tag{
    position: absolute;
    top: -0.75rem;
    left: 1.25rem;
    font-size: var(--smaller-font-size);
    padding: 0.25rem;
    background-color: var(--body-color);
    z-index: 10;
}
.contact_form-area{
    height: 11rem;
}
.contact_form-area textarea{
    resize: none;
}
@media screen and (max-width: 793px) {
    .contact_info{
        grid-template-columns: 100%;
    }
    .contact_form{
        width: 100%;
    }
}